.best-sales {
  width: 1200px;
  box-sizing: border-box;
  padding: 28px 15px 20px 15px;
  margin-left: auto;
  margin-right: auto;
}

.best-sales__name {
  font-size: 36px;
  font-weight: 800;
  line-height: 43.2px;
  margin: 0;
  text-align: center;
}

.best-sales__name span {
  color: #ff5912;
}

.best-sales__text {
  color: #495662;
  font-size: 16px;
  line-height: 30px;
  text-align: center;
  margin-bottom: 70px;
}

.best-sales__wrapper {
  display: flex;
}

.best-sales__card {
  width: 370px;
  height: 160px;
  background-color: #ffffff;
  box-sizing: border-box;
  margin: 0;
  padding: 35px 30px;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.best-sales__card:not(:nth-child(3n)) {
  margin-right: 30px;
}

.best-sales__card-wrapper p {
  font-size: 18px;
  font-weight: 700;
  line-height: 21.6px;
}

.best-sales__card-name {
  margin-top: 13px;
  margin-bottom: 0;
}

.best-sales__card-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 27px;
}

.best-sales__card-info img {
  margin-left: -7px;
}

.best-sales__card-info span {
  font-size: 16px;
  line-height: 19.2px;
  color: #ff5912;
}
